const prizes = [
  {
    text: '맥북 Air 13 ',
    image: 'https://i.ibb.co/6Z6Xm9d/good-1.png',
  },
  {
    text: '아이폰 15 Pro ',
    image: 'https://i.ibb.co/T1M05LR/good-2.png',
  },
  {
    text: '맥북 프로 ',
    image: 'https://i.ibb.co/Qbm8cNL/good-3.png',
  },
  {
    text: 'IPad air 5세대 ',
    image: 'https://i.ibb.co/5Tpfs6W/good-4.png',
  },
  {
    text: '맥북 프로 M3 ',
    image: 'https://i.ibb.co/64k8D1c/good-5.png',
  },
];

const winPrizeIndex = 0;

const reproductionArray = (array = [], length = 0) => [
  ...Array(length)
    .fill('_')
    .map(() => array[Math.floor(Math.random() * array.length)]),
];

const reproducedPrizeList = [
  ...prizes,
  ...reproductionArray(prizes, prizes.length * 3),
  ...prizes,
  ...reproductionArray(prizes, prizes.length),
];

const generateId = () =>
  `${Date.now().toString(36)}-${Math.random().toString(36).substring(2)}`;

const prizeList = reproducedPrizeList.map((prize) => ({
  ...prize,
  id: typeof crypto.randomUUID === 'function' ? crypto.randomUUID() : generateId(),
}));

const App = () => {
  //우선적으로 일단? 아이피 불러오고 => 접속한 아이디랑 대조해서 스타트 버튼 체크
  // 우선 바로 해야할꺼 저거 좀 꾸미는건 나중에하고
  // 데이터 삽입하는게 우선임

  const [ ipCheck, setIpCheck ] = useState(false)
  const [ ipList, setIpList ] = useState([]);
  const [ accessIp, setAccessIp] = useState('');

  useEffect( () => {
    fetchClients();

    fetchClientIP();


    if(accessIp) {
      ipList.forEach((ip) => {
        console.log('있는 ip', ip)
        console.log('접속한 ip', accessIp)
        if(ip === accessIp) {
          setIpCheck(false)  
        }
      })
    }

  }, [accessIp])

  const fetchClientIP = async () => {
    try {
      const response = await fetch('https://api.ipify.org/?format=json');
      const data = await response.json();
      const clientIP = data.ip;
      setAccessIp(clientIP);
    } catch (error) {
      console.error('Error fetching client IP:', error);
    }
  }

  const fetchClients = async () => {
    const clients = collection(db, 'clients')
    const docSnap = await getDocs(clients)

    docSnap.forEach((doc) => {
      //우선 데이터는 doc._document.data.value.mapValue.fields.ip 에 있긴함
      // setIpList(prev => [...prev, doc.data().ip])
      const ip = doc.data().ip;
      setIpList(prev => {
        const ipSet = new Set([...prev, ip]);
        return Array.from(ipSet);
      });
      console.log('#####', ipList)
    })

  }

  const [start, setStart] = useState(false);
  const [loadingCheck, setLoadingCheck] = useState(false);
  const [showPrizeModal, setShowPrizeModal] = useState(false);

  const [index, setIndex] = useState(0);

  const prizeIndex = prizes.length * 4 + winPrizeIndex;
  
  const handleStart = () => {
    setLoadingCheck(true);
    setStart((prevState) => !prevState);
  };

  
  // 종료되는 함수
  const handlePrizeDefined = (item) => {

    onChangeModal();
    setLoadingCheck(false);
    console.log('🥳 Prize defined! 🥳', item);

    console.log(prizeList[index])

    //모달 켜야하는데 그럴싸한 애니메이션 없나
    
  };

  const onChangeModal = useCallback(() => {
    setShowPrizeModal(true);
    console.log("showPrizeModal : ",showPrizeModal)
    console.log(prizeList[prizeIndex])
  }, [showPrizeModal])

  return (
      <>
        <div className='roulette_wrap'>
        <RoulettePro
          
          start={start}
          prizes={prizeList}
          prizeIndex={prizeIndex}
          spinningTime={3}
          onPrizeDefined={handlePrizeDefined}
          designOptions={{hideCenterDelimiter: true,}}
          soundWhileSpinning="https://react-roulette-pro.ivanadmaers.com/assets/f3722b4574da2a35a4ef.mp3"
          options={{ stopInCenter: true, withoutAnimation: true,  }}
          defaultDesignOptions={{ prizesWithText: true }}
        />
          
          {ipCheck  
            ? <button disabled={true} onClick={handleStart} className="btn-container" style={{backgroundColor: 'transparent', border: 'none'}}>
            <a href="#" className="btn-3d yellow">Button</a>
          </button>
            : <button disabled={loadingCheck} onClick={handleStart} className="btn-container" style={{backgroundColor: 'transparent', border: 'none'}}>
              <a href="#" className="btn-3d yellow">Button</a>
            </button>
          }
        </div>
        
        {
          showPrizeModal && 
          <>
            <Canvas/>
              <div className="prize">
                <div className="prize-content">
                  <h2>축하합니다!</h2>
                  <div className='divider' />
                  <h4 style={{margin: '5px'}}>{prizeList[prizeIndex].text} 당첨</h4>
                  <div>
                    <img width={200} height={200} src={prizeList[prizeIndex].image} />
                  </div>
                  <button class="btn-hover color-6">
                    <a href="https://goat-v.com" target='_blank' style={{fontSize: '18px', margin: '0px', color: '#fff'}}>캡쳐하고 상품 수령하기</a>
                  </button>
                </div>
            </div>
          </>

        }
        
      </>
  );
};
export default App;